<template>
    <div>
      <!-- Í≤ÄÏÉâÏñ¥ ÏãúÏûë -->
      <div class="col-md-8">
        <div class="input-group mb-3">
          <!-- Í≤ÄÏÉâÏñ¥ ÏûÖÎ†• -->
          <input
            type="text"
            class="form-control"
            placeholder="Search by Title"
            v-model="searchTitle"
          />
          <!-- Í≤ÄÏÉâÏñ¥ Î≤ÑÌäº -->
          <div class="input-group-append">
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="
                page = 1;
                retrieveGallery();
              "
            >
              Search
            </button>
          </div>
        </div>
      </div>
      <!-- Í≤ÄÏÉâÏñ¥ ÎÅù -->
  
      <!-- Upload ÏãúÏûë -->
      <div class="row justify-content-md-center">
        <div class="col-12">
          <!-- ÌéòÏù¥Ïßï ÏñëÏãù ÏãúÏûë -->
          <div class="col-md-12">
            <!-- ÏÖÄÎ†âÌä∏ Î∞ïÏä§ : Í∏∞Î≥∏ ÌéòÏù¥ÏßÄ Î≥ÄÍ≤Ω -->
            <!-- change :  handlePageSizeChange($event), 1ÌéòÏù¥ÏßÄÎãπ Í∞úÏàò Î≥ÄÍ≤Ω Ïãú Ïã§ÌñâÎêòÎäî Ïù¥Î≤§Ìä∏-->
            <!--           $event : html ÌÉúÍ∑∏Ïùò Í∏∞Î≥∏ Ïù¥Î≤§Ìä∏, Ïù¥ Í∞ùÏ≤¥Î°ú ÌòÑÏû¨ ÏÑ†ÌÉù ÎòêÎäî ÌÅ¥Î¶≠Ìïú htmlÌÉúÍ∑∏(ÏñëÏãù)Î•º Ïïå Ïàò ÏûàÏùå.
                         event.target (ÌòÑÏû¨ ÏÇ¨Ïö©ÏûêÍ∞Ä ÏÑ†ÌÉùÌïú ÏñëÏãù(Ïó¨Í∏∞ÏÑúÎäî ÏÖÄÎ†âÌä∏ Î∞ïÏä§ÎÇ¥ Î™©Î°ùÏùÑ ÏùòÎØ∏ ) -->
            <div class="mb-3">
              Items per Page:
              <select v-model="pageSize" @change="handlePageSizeChange($event)">
                <option v-for="size in pageSizes" :key="size" :value="size">
                  {{ size }}
                </option>
              </select>
            </div>
  
            <!-- b-pagination : Î∂ÄÌä∏Ïä§Ìä∏Îû© - ÌéòÏù¥ÏßÄ Î≤àÌò∏ Ïª®Ìä∏Î°§ -->
            <!-- total-rows : Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞ Í∞úÏàò -->
            <!-- per-page : 1ÌéòÏù¥ÏßÄ Îãπ Í∞úÏàò -->
            <!-- change : handlePageChange(), ÌéòÏù¥ÏßÄ Î≤àÌò∏ Î≥ÄÍ≤Ω Ïãú Ïã§ÌñâÎêòÎäî Ïù¥Î≤§Ìä∏ -->
            <b-pagination
              v-model="page"
              :total-rows="count"
              :per-page="pageSize"
              prev-text="Prev"
              next-text="Next"
              @change="handlePageChange"
            ></b-pagination>
          </div>
          <!-- ÌéòÏù¥Ïßï ÏñëÏãù ÎÅù -->
  
          <!-- Ïù¥ÎØ∏ÏßÄÎ™Ö(galleryTitle) ÏûÖÎ†•Î∞ïÏä§ ÏãúÏûë -->
          <div class="mb-3 col-md-5">
            <label for="galleryTitle" class="form-label">Gallry Title</label>
            <input
              type="text"
              class="form-control"
              id="galleryTitle"
              required
              name="galleryTitle"
              v-model="galleryTitle"
            />
          </div>
          <!-- Ïù¥ÎØ∏ÏßÄÎ™Ö ÏûÖÎ†•Î∞ïÏä§ ÎÅù -->
  
          <!-- Ïù¥ÎØ∏ÏßÄÎÇ¥Ïö© ÏûÖÎ†•Î∞ïÏä§ ÏãúÏûë -->
          <div class="mb-3 col-md-5">
            <label for="galleryFileName" class="form-label">Gallery Name</label>
            <input
              type="text"
              class="form-control"
              id="galleryFileName"
              required
              name="galleryFileName"
              v-model="galleryFileName"
            />
          </div>
          <!-- Ïù¥ÎØ∏ÏßÄÎÇ¥Ïö© ÏûÖÎ†•Î∞ïÏä§ ÎÅù -->
  
          <!-- Ïù¥ÎØ∏ÏßÄ ÏÑ†ÌÉùÏÉÅÏûê ÏãúÏûë -->
          <div class="mb-3 col-md-5">
            <label class="btn btn-default p-0">
              <!-- <!‚Äî ÌååÏùº ÏÑ†ÌÉùÏÉÅÏûê ‚Äî> -->
              <input
                type="file"
                accept="image/*"
                ref="file"
                @change="selectGallery"
              />
            </label>
          </div>
          <!-- Ïù¥ÎØ∏ÏßÄ ÏÑ†ÌÉùÏÉÅÏûê ÎÅù -->
  
          <!-- upload Î≤ÑÌäº : insert Î¨∏ Ïã§Ìñâ ÏãúÏûë -->
          <div class="mb-3">
            <!-- <!‚Äî ÏÑúÎ≤ÑÏóê insert Î¨∏ Ìò∏Ï∂ú ‚Äî> -->
            <button
              class="btn btn-success btn-sm float-left"
              :disabled="!currentGallery"
              @click="upload"
            >
              Upload
            </button>
          </div>
          <!-- upload Î≤ÑÌäº : insert Î¨∏ Ïã§Ìñâ ÎÅù -->
        </div>
      </div>
      <!-- Upload ÎÅù -->
  
      <!-- ÎØ∏Î¶¨Î≥¥Í∏∞ Ïù¥ÎØ∏ÏßÄ ÏãúÏûë -->
      <div v-if="previewGallery">
        <div>
          <img class="preview my-3" :src="previewGallery" alt="" />
        </div>
      </div>
      <!-- ÎØ∏Î¶¨Î≥¥Í∏∞ Ïù¥ÎØ∏ÏßÄ ÎÅù -->
  
      <!-- ÏÑúÎ≤Ñ ÏóêÎü¨ Î©îÏÑ∏ÏßÄÍ∞Ä ÏûàÏùÑ Í≤ΩÏö∞ ÏïÑÎûò Ï∂úÎ†• ÏãúÏûë -->
      <div v-if="message" class="alert alert-secondary" role="alert">
        {{ message }}
      </div>
      <!-- ÏÑúÎ≤Ñ ÏóêÎü¨ Î©îÏÑ∏ÏßÄÍ∞Ä ÏûàÏùÑ Í≤ΩÏö∞ ÏïÑÎûò Ï∂úÎ†• ÎÅù -->
  
      <!-- <!‚Äî ÏáºÌïë Ïπ¥Ìä∏ ÌòïÌÉú ÎîîÏûêÏù∏ ÏãúÏûë ‚Äî> -->
      <!-- <!‚Äî v-for ÏãúÏûë ‚Äî> -->
      <div class="row">
        <!-- üëÄ col : Ïó¥. 12Ïπ∏Ïù¥ ÏµúÎåÄ. 4Î°ú Î∂ÑÌï†ÌïòÎ©¥ Ïù¥ÎØ∏ÏßÄ 3Í∞úÏî© Î≥¥Ïó¨Ï§å -->
        <div class="col-sm-4" v-for="(data, index) in gallery" :key="index">
          <div class="card">
          <!-- data.galleryUrl forÎ¨∏ ÎèåÏïÑÍ∞à Îïå Î∞±ÏóîÏóêÏÑú Ïù¥ÎØ∏ÏßÄÌååÏùºÎã§Ïö¥Î°úÎìúÌï®Ïàò getFile(gid) Ïã§ÌñâÎê®üòµ -->
            <img :src="data.galleryUrl" class="card-img-top" alt="Í∞ïÏùò" />
            <div class="card-body">
              <h5 class="card-title">{{ data.galleryTitle }}</h5>
              <p class="card-text">
                {{ data.galleryFileName }}
              </p>
              <a style="color: inherit" @click="deleteImage(data.gid)">
                <!-- <!‚Äî <i class="fas fa-trash" /> ‚Äî> -->
                <!-- Î≤ÑÌäºÏúºÎ°ú Î∞îÍøîÎèÑÎê® Ïù¥Í±¥ ÌÖçÏä§Ìä∏ badgeÏûÑ Í∑∏ÎûòÏÑú Î∞òÏùë ÏïΩÍ∞Ñ ÎäêÎ¶º -->
                <span class="badge bg-danger">Delete</span>
                <!-- <button>ÏÇ≠Ï†ú</button> -->
  
              </a>
            </div>
          </div>
        </div>
      </div>
      <!-- <!‚Äî ÏáºÌïë Ïπ¥Ìä∏ ÌòïÌÉú ÎîîÏûêÏù∏ ÎÅù ‚Äî> -->
    </div>
  </template>
  
  <script>
  // axios Í≥µÌÜµ Ìï®Ïàò import
  import GalleryDataService from "../../services/GalleryDataService";
  
  export default {
    data() {
      return {
        currentGallery: undefined, // ÌòÑÏû¨ Ïù¥ÎØ∏ÏßÄ Î≥ÄÏàò
        previewGallery: undefined, // ÎØ∏Î¶¨Î≥¥Í∏∞ Ïù¥ÎØ∏ÏßÄ Î≥ÄÏàò
        message: "", // ÏÑúÎ≤ÑÏ™ΩÏóêÏÑú ÏóêÎü¨Í∞Ä ÎÇ† Îïå Î≥¥ÎÇ¥Îäî Î©îÏãúÏßÄÎ•º Ï†ÄÏû•Ìï† Î≥ÄÏàò
        gallery: [], // Ïù¥ÎØ∏ÏßÄ Í∞ùÏ≤¥ Î∞∞Ïó¥(Ïó¨Îü¨Í∞ú)Ïù¥ ÎÇ†ÏïÑÏò§Î©¥ Ï†ÄÏû•ÎêòÎäî Í≥≥üëÄ
        searchTitle: "", // Í∞§Îü¨Î¶¨Ïù¥Î¶ÑÏúºÎ°ú Í≤ÄÏÉâÌïòÎäî Î≥ÄÏàò
  
        // springboot ÏöîÏ≤≠Ìï† Î≥ÄÏàò , Ïù¥ÎØ∏ÏßÄÎ™Ö(galleryTitle), ÎÇ¥Ïö©(content)
        galleryTitle: "",
        galleryFileName: "",
  
        // ÌéòÏù¥ÏßïÏùÑ ÏúÑÌïú Î≥ÄÏàò Ï†ïÏùò
        page: 1, // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ
        count: 0, // Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞ Í±¥Ïàò
        pageSize: 3, // ÌïúÌéòÏù¥ÏßÄÎãπ Î™áÍ∞úÎ•º ÌôîÎ©¥Ïóê Î≥¥Ïó¨Ï§ÑÏßÄ Í≤∞Ï†ïÌïòÎäî Î≥ÄÏàò
  
        pageSizes: [3, 6, 9], // select box Ïóê ÎÑ£ÏùÑ Í∏∞Î≥∏ Îç∞Ïù¥ÌÑ∞
      };
    },
    methods: {
      // Ï°∞Ìöå Ìï®Ïàò
      retrieveGallery() {
        GalleryDataService.getGallerys(this.searchTitle, this.page - 1, this.pageSize) // ÌîÑÏóî ÏûêÎèô ÌéòÏù¥ÏßïÏ≤òÎ¶¨Í∞Ä 1Î∂ÄÌÑ∞ ÏãúÏûëÌïòÎØÄÎ°ú -1Ìï¥ÏÑú 0ÏúºÎ°ú ÎßåÎì§Ïñ¥ Ï§å(Ïä§ÌîÑÎßÅÎ∂ÄÌä∏Îäî 0Î≤àÎ∂ÄÌÑ∞ Îß§Í∏∞ÎãàÍπå ÎßûÏ∂∞Ï§å)
          //  axiosÏÑ±Í≥µÌïòÎ©¥ Ïä§ÌîÑÎßÅÎ∂ÄÌä∏ Í≤∞Í≥ºÍ∞Ä .then() ÏïàÏúºÎ°ú Ï†ÑÏÜ°Îê®
          .then((response) => {
            // const(let){ÏÜçÏÑ±Î™Ö, ÏÜçÏÑ±Î™Ö2} = Îç∞Ïù¥ÌÑ∞Í∞ùÏ≤¥Î∞∞Ïó¥  : Î™®ÎçòÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏ Î¨∏Î≤ï Íµ¨Ï°∞Î∂ÑÌï¥Ìï†Îãπ
            const { gallery, totalItems } = response.data; //  springboot ÏóêÏÑú Ï†ÑÏÜ°Îêú Îßµ(map) Ï†ïÎ≥¥
            this.gallery = gallery; // Ïä§ÌîÑÎßÅÎ∂ÄÌä∏ÏóêÏÑú Ï†ÑÏÜ°Ìïú Îç∞Ïù¥ÌÑ∞
            this.count = totalItems; // Ïä§ÌîÑÎßÅÎ∂ÄÌä∏ÏóêÏÑú Ï†ÑÏÜ°Ìïú ÌéòÏù¥ÏßÄÏ†ïÎ≥¥(Ï¥ù Í±¥Ïàò)
            // ÎîîÎ≤ÑÍπÖ ÏΩòÏÜîÏóê Ï†ïÎ≥¥ Ï∂úÎ†•
            console.log(response.data);
          })
          // Ïã§Ìå®ÌïòÎ©¥ .catch() Ïóê ÏóêÎü¨Í∞Ä Ï†ÑÏÜ°Îê®
          .catch((e) => {
            console.log(e);
          });
      },
      // ÌååÏùº ÏÑ†ÌÉùÏÉÅÏûêÏóêÏÑú ÏÑ†ÌÉùÌïú Ïù¥ÎØ∏ÏßÄÎ•º Ï†ÄÏû•ÌïòÎäî Ìï®Ïàò
      selectGallery() {
        // Ï≤´Î≤àÏß∏ ÏÑ†ÌÉùÌïú Ïù¥ÎØ∏ÏßÄÎ•º Î≥ÄÏàòÏóê Ï†ÄÏû•
        //  this.$refs : htmlÏóêÏÑú $refs ÏÜçÏÑ±Ïù¥ ÏûàÎäî Ïª®Ìä∏Î°§Ïù¥ ÏÑ†ÌÉùÎê®(Î∞îÎ°úÏ†ëÍ∑ºÍ∞ÄÎä•)_Ïù¥ÎØ∏ÏßÄ ÏÑ†ÌÉùÏÉÅÏûêÏùò inputÎ∞ïÏä§Ïóê ref Í±∏Î†§ÏûàÏùå(ref="gallery")
        //               refs.gallery Ï§ëÏóê Ïù¥Î¶ÑÏù¥ GallerysÏù∏ Ïï† ÏÑ†ÌÉù
        this.currentGallery = this.$refs.file.files.item(0); // Î∞∞Ïó¥ Ï§ë Ï≤´Î≤àÏß∏ ÏÑ†ÌÉùÌïú Ïù¥ÎØ∏ÏßÄ(0)Î•º ÌòÑÏû¨ Ïù¥ÎØ∏ÏßÄ Î≥ÄÏàò
        // .createObjectURL() : Ïù¥ÎØ∏ÏßÄ Ï£ºÏÜåÎßå Ï∞∏Ï°∞Ìï¥ÏÑú Ïù¥ÎØ∏ÏßÄ Î≥¥Ïó¨Ï£ºÍ∏∞ Ìï®Ïàò
        this.previewGallery = URL.createObjectURL(this.currentGallery);
        this.message = "";
      },
      //  upload Ìï®Ïàò : Î≥ÄÏàò 3Í∞úÏùò Îç∞Ïù¥ÌÑ∞Î•º axiosÎ•º ÌÜµÌï¥ Ï†ÄÏû•üò¶
      upload() {
        GalleryDataService.upload(
          this.galleryTitle,
          this.galleryFileName,
          this.currentGallery
        )
          // insert ÏÑ±Í≥µ then()
          .then((response) => {
            // ÏÑúÎ≤ÑÏ™Ω ÏÑ±Í≥µ Î©îÏÑ∏ÏßÄÎ•º Ï†ÄÏû•
            this.message = response.data.message;
  
            // Ïû¨Ï°∞Ìöå ÏöîÏ≤≠(axios Ï°∞ÌöåÌï®Ïàò .getGallerys()) : Îç∞Ïù¥ÌÑ∞ Ïûò Îì§Ïñ¥ÏôîÎäîÏßÄ ÌôîÎ©¥Ïóê Ï∂úÎ†• ÏöîÏ≤≠
            return GalleryDataService.getGallerys(
              this.searchTitle,
              this.page - 1,
              this.pageSize
            );
          })
          // Ï°∞ÌöåÍ∞Ä ÏÑ±Í≥µÌïòÎ©¥ Ïã§ÌñâÎêòÎäî then()
          .then((response2) => {
            const { gallery, totalItems } = response2.data;
            this.gallery = gallery;
            this.count = totalItems;
            console.log(response2.data);
          })
          .catch((e) => {
            console.log(e);
            // ÏÑúÎ≤Ñ Ï™ΩÏóêÏÑú Ïã§Ìå® Î©îÏãúÏßÄÎ•º Î∞õÏïÑÏÑú ÌôîÎ©¥Ïóê Ï∂úÎ†•
            this.message = "Could not upload the image!" + e;
            this.currentGallery = undefined;
          });
      },
      // select box Í∞í Î≥ÄÍ≤ΩÏãú Ïã§ÌñâÎêòÎäî Ìï®Ïàò(Ïû¨Ï°∞Ìöå)
      handlePageSizeChange(event) {
        this.pageSize = event.target.value; // ÌïúÌéòÏù¥ÏßÄÎãπ Í∞úÏàò Ï†ÄÏû•(3, 6, 9)
        this.page = 1;
        // Ïû¨Ï°∞Ìöå Ìï®Ïàò Ìò∏Ï∂ú
        this.retrieveGallery();
      },
      // ÌéòÏù¥ÏßÄ Î≤àÌò∏ Î≥ÄÍ≤ΩÏãú Ïã§ÌñâÎêòÎäî Ìï®Ïàò(Ïû¨Ï°∞Ìöå)
      handlePageChange(value) {
        this.page = value; // Îß§Í∞úÎ≥ÄÏàòÍ∞íÏúºÎ°ú ÌòÑÏû¨ÌéòÏù¥ÏßÄ Î≥ÄÍ≤Ω
        // Ïû¨Ï°∞Ìöå Ìï®Ïàò Ìò∏Ï∂ú
        this.retrieveGallery();
      },
      // gidÎ°ú ÏÇ≠Ï†úÌïòÎäî Ìï®Ïàò
      deleteImage(gid) {
        // axios Í≥µÌÜµÌï®Ïàò Ìò∏Ï∂ú
        GalleryDataService.delete(gid)
          // ÏÑ±Í≥µÌïòÎ©¥ then() Í≤∞Í≥ºÍ∞Ä Ï†ÑÏÜ°Îê®
          .then((response) => {
            console.log(response);
            // Ï≤´ÌéòÏù¥ÏßÄ(Ï†ÑÏ≤¥Î™©Î°ù_Ï°∞Ìöå_ÌéòÏù¥ÏßÄ) Í∞ïÏ†ú Ïù¥Îèô : /gallery
            this.message = "Ï†ïÏÉÅÏ†ÅÏúºÎ°ú ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.";
            // ÏÇ≠Ï†ú ÌõÑ Ïû¨Ï°∞Ìöå
            this.retrieveGallery();
          })
          // Ïã§Ìå®ÌïòÎ©¥ .catch() ÏóêÎü¨Î©îÏÑ∏ÏßÄÍ∞Ä Ï†ÑÏÜ°Îê®
          .catch((e) => {
            console.log(e);
            this.message = "Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§." + e.message;
          });
      },
    },
    // ÌôîÎ©¥Ïù¥ Îú®Ïûê ÎßàÏûê Ïã§ÌñâÎêòÎäî Ïù¥Î≤§Ìä∏ - Ï†ÑÏ≤¥Ï°∞Ìöå Ìï®Ïàò retrieveGallery() Ïã§Ìñâ
    mounted() {
      this.retrieveGallery();
    },
  };
  </script>
  
  <style>
      .preview {
          max-width: 200px;
      }
  
  </style>
  