// GalleryDataService.js
import http from "../http-common";

class GalleryDataService {
    // Ìï®ÏàòÎ™Ö : upload() Ìï®Ïàò ( DB Ï†ÄÏû• Ìï®Ïàò )
    // Îß§Í∞úÎ≥ÄÏàò title : Ï†úÎ™©
    //          Name : ÎÇ¥Ïö©
    //          Gallery : ÏóÖÎ°úÎìú ÎåÄÏÉÅ Í∞§Îü¨Î¶¨Í∞Ä Ï†ÄÏû•ÎêòÎäî Í∞ùÏ≤¥ Î∞∞Ïó¥
    upload(galleryTitle, galleryFileName, gallery ) {    // üò¶
        //                                Í∞ùÏ≤¥üëÄ
        // json Í∞ùÏ≤¥ ÏÇ¨Ïö©ÌïòÏßÄ ÏïäÏùå (x)
        // form ÌÉúÍ∑∏Î°ú Ï†ÑÏÜ°ÌïòÎäî Î∞©ÏãùÏùÑ Ïù¥Ïö©Ìï®
        // html <form></form> == js FormData Í∞ùÏ≤¥Î°ú ÏÇ¨Ïö©Í∞ÄÎä•
        //                       .append("ÏÜçÏÑ±Î™Ö", Í∞í) Ìï®Ïàò : Îç∞Ïù¥ÌÑ∞Î•º Ï†ÄÏû•
        // axios Ìï®Ïàò : .post()
        // Ìó§Îçî : "Name-Type" : "multipart/form-data" 
        let formData = new FormData(); // Ìèº(form) Í∞ùÏ≤¥ ÏÉùÏÑ±

        formData.append("galleryTitle", galleryTitle);
        formData.append("galleryFileName", galleryFileName);
        formData.append("gallery", gallery);

        return http.post("/gallery/upload", formData, {
            headers: {
                "Content-Type" : "multipart/form-data"
            }
        })
    }

    // Ïù¥ÎØ∏ÏßÄ Ï†úÎ™© Í≤ÄÏÉâ ÏöîÏ≤≠ Ìï®Ïàòü§©
    getGallerys(galleryTitle, page, size) {
        console.log(galleryTitle);
        console.log(page);
        console.log(size);
        return http.get(`/gallery?galleryTitle=${galleryTitle}&page=${page}&size=${size}`)
    }

    // Ïù¥ÎØ∏ÏßÄ ÏÇ≠Ï†ú ÏöîÏ≤≠ Ìï®Ïàò
    delete(gid) {
        return http.delete(`/gallery/deletion/${gid}`);
    }
    

}

export default new GalleryDataService();